body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

body:before {
  content: "";
  position: fixed; /* This will not scroll with the page. */
  z-index: -1; /* Make sure it appears behind any content. */
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden; /* This is what promotes it to its own layer. */
  background: rgb(238,174,202);
  background: -webkit-radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%);
  background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#eeaeca",endColorstr="#94bbe9",GradientType=1);
}

footer {
    width: 500px;
    margin: auto;
    margin-top: 5px;
    text-align: center;
    font-family: 'Courier New', Courier, monospace;
    color: white;
}
.container {
    --numbers-color: #f8eac0;
    --operations-color: #7fbbab;
    --buttons-box-shadow: 2px 2px 2px #3f3f3f;
    --buttons-font-color: #232323;
    font-size: 30px;
    font-family: 'Nova Mono', monospace;
    text-align: center;
    min-height: 400px;
    width: 300px;
    margin: auto;
    margin-top: 100px;
    background: #64696b;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 20% 1fr;
    grid-gap: 10px;
    padding: 10px;
    grid-template-areas: 
        "screens"
        "buttons";
    box-shadow: 4px 4px 1px #3f3f3f;
}

#screens {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 1fr;
    text-align: end;
    background-color: #2a2c31;
    font-family: 'VT323', monospace;
}

#operScreen { 
    font-size: 25px;
    padding-right: 8px;
    color: #19d209;
}

#calcScreen {
    font-size: 35px;
    padding-right: 8px;
    color: #19d209;
}

#buttons {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
    grid-gap: 7px;
}

#clear { 
    background:#f66c77;
    grid-column: 1 / 3;
    box-shadow: var(--buttons-box-shadow);
    color: var(--buttons-font-color);
    display: flex;
    justify-content: center;
    align-items: center;
}
